*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  position: relative;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  color: #121212;
}

h1 {
  margin-bottom: 24px;
  font-size: 3rem;
  font-weight: 700;
  line-height: 120%;
}

p {
  line-height: 150%;
}

main {
  min-height: 100vh;
  max-width: 1440px;
  margin: 0 auto;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
}

.btn {
  padding: 16px 40px;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  background-color: #E4C1AE;
  border: none;
  border-radius: 32px;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}
.btn:hover {
  background-color: #573726;
  box-shadow: 0px 0px 10px #573726;
}

.first-section {
  padding: 80px 40px;
  text-align: center;
}
.first-section__box {
  display: flex;
  flex-direction: column;
}
.first-section__img {
  width: 100%;
  margin-bottom: 40px;
}
.first-section__title {
  width: 100%;
}
.first-section__text {
  margin-bottom: 32px;
}

.pop-up {
  display: none;
  z-index: 100;
}
.pop-up__background {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  background-color: #121212;
  opacity: 0.5;
}
.pop-up__window {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 45px;
  width: 80vw;
  min-height: 210px;
  background-color: #FFFFFF;
}
.pop-up__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.pop-up__header .close-btn {
  background: none;
  border: none;
  border-radius: 32px;
  cursor: pointer;
  transition: color 0.3s, background 0.3s;
}
.pop-up__header .close-btn:hover {
  color: #FFFFFF;
  background: #573726;
}
.pop-up__header .close-btn .fa-times {
  padding: 5px 10px;
  font-size: 2rem;
}
.pop-up__info {
  font-size: 1.4rem;
  margin-bottom: 20px;
}
.pop-up__reset-btn {
  background: none;
  color: #E4C1AE;
  border: 1px solid #E4C1AE;
  transition: color 0.3s, background 0.3s, border 0.3s;
}
.pop-up__reset-btn:hover {
  color: #573726;
  background: #E4C1AE;
  border: 1px solid #573726;
}